<template>
    <header class="header">
        <a v-for="(logo, index) in logoItem" :key="`logo - ${index}`" :href="logo.href" :class="logo.class_logo">
            <img src="./image/logo.png" alt="">
        </a>

        <nav class="navbar" >
            <div id="marker"></div>
            <a v-for="(nav, index) in navItem" :key="`nav - ${index}`" @click="showNavIndicator()" :href="nav.href" >
            <!-- <a v-for="(item, index) in navItems" :key="`nav-${index}`" @click="showNavIndicator()" :href="item.href"> -->
                {{nav.text}}
            </a>
        </nav>

        <div class="icons">
            <div id="btn-bar" @click="slideNavBar()" class="fa-solid fa-bars"></div>
            <div v-for="(icons, index) in iconItem" :key="`icons - ${index}`" :id="icons.id"  :class="icons.icon"></div>
        </div>
    </header>
</template>

<script>
export default {
    name:"comp-header",
    data() {
        return{
            logoItem: [
                {
                    class_logo:"logo",
                    href:"#"
                }
            ],

            navItem: [
                {
                    text: "Home",
                    href: "#home"
                },

                {
                    text: "About",
                    href: "#about"
                },

                {
                    text: "Shop",
                    href: "#shop"
                },

                {
                    text: "Portfolio",
                    href: "#portfolio"
                },

                {
                    text: "Blog",
                    href: "#blog"
                }
            ],

            iconItem: [

                {
                    id:"btn-cart",
                    icon:"fa-solid fa-cart-shopping"
                },

                {
                    id:"btn-search",
                    icon:"fa-solid fa-magnifying-glass"
                }
            ]
        }
    },

    methods: {
        showNavIndicator() {
            var marker = document.querySelector("#marker");
            var item = document.querySelectorAll(".navbar a");

            function indicator(e) {
                marker.style.left = e.offsetLeft + "px";
                marker.style.width = e.offsetWidth + "px";
            }

            item.forEach((details) => {
                details.addEventListener("click", (e) => {
                indicator(e.target);
                });
            });
        },

        slideNavBar() {
            var navbar = document.querySelector(".navbar");
            var btnBar = document.querySelector("#btn-bar");

            function showDown() {
                navbar.classList.toggle('active')
            }

            btnBar.addEventListener('click', showDown)

        },
        
    },

    
};
</script>

<style lang="scss">
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  text-transform: none;
}

html {
  font-size: 62.5%;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1 !important;
    box-shadow: 0 .5rem 1rem rgba(0,0,0, .3);
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 7%;

    .logo {
        img {
            // height: 100%;
            // max-height: 5.6rem;
            max-width: 100%;
            width: 8rem;
        }
    }

    .navbar {
        position: relative;

        #marker {
            position: absolute;
            left: 0;
            bottom: -0.8rem;
            height: 0.4rem;
            width: 0;
            background: #333;
            transition: 0.5s;
            border-radius: 0.5rem;
        }

        a {
            position: relative;
            font-size: 1.5rem;
            padding: 2rem 1rem;
            border-radius: 1rem;
            margin: 0 2rem;
            color: #333;
        }
    }

    .icons {
        display: flex;

        #btn-bar,
        #btn-cart,
        #btn-search {
                font-size: 1.7rem;
            padding: .5rem;
            margin: 0 .5rem;
            

            &:hover {
                cursor: pointer;
            }
        }

        #btn-bar{
            display: none;
        }
    }
}

// media queries 

@media (max-width:991px){

  html{
    font-size: 55%;
  }

  .header{
    padding:2rem;
  }

  section{
    padding:2rem;
  }

}

@media (max-width:768px){

    .header{
        .navbar{
        position: absolute;
        top:99%; left:0; right: 0;
        background: #fff;
        border-top: .1rem solid rgba(0,0,0,.2);
        border-bottom: .1rem solid rgba(0,0,0,.2);
        clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);

        &.active{
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
        }

            a{
                font-size: 1.5rem;
                display: block;
            }
        }

        .icons {
            #btn-bar{
                display: block;
            }
        }
    }
  

}

@media (max-width:450px){

  html{
    font-size: 50%;
  }


}
</style>